.profile__info {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  justify-content: flex-start;
  align-items: baseline;
  column-gap: 18px;
}

@media (max-width: 730px) {
  .profile__info {
    justify-content: center;
    column-gap: 10px;
  }
}
